const ISS_BASE_API = 'https://api.wheretheiss.at/v1/satellites/25544';

export const getCurrentISSLocation = () => (
  fetch(ISS_BASE_API)
    .then((response) => (
      response
        .json()
        .then((json) => (response.ok ? Promise.resolve(json) : Promise.reject(json)))
    ))
);

export default getCurrentISSLocation;
